.form {
    max-width: 620px;
    width: 100%;
    gap: 20px;
    position: relative;

    input {
        flex: 1;
    }

    button {
        color: var(--text);
        font-size: 18px;
        transition: all var(--transition);
        line-height: 1;

        &:hover, &:focus {
            color: var(--accent);
        }

        &[type="reset"] {
            visibility: hidden;
            opacity: 0;

            &.visible {
                visibility: visible;
                opacity: 1;
            }
        }
    }
}
.container {
    position: relative;
  }
  
  .results {
    position: absolute;
    top: 100%;
    left: 0;
    width: 100%;
    max-height: 200px;
    overflow-y: auto;
    background-color: rgb(0, 0, 0);
    border-radius: 10px;
    box-shadow: 0 0 10px rgba(255, 255, 255, 0.1);
    padding: 0;
    margin: 0;
    z-index: 1;
  }
  
  .results li {
    padding: 8px;
    border-bottom: 1px solid rgb(0, 0, 0);
    font-size: 14px;
    cursor: pointer;
  }
  
  .results li:last-child {
    border-bottom: none;
  }
  